import emailjs from '@emailjs/browser';

// EmailJS configuration
const EMAILJS_SERVICE_ID = process.env.REACT_APP_EMAILJS_SERVICE_ID;
const EMAILJS_TEMPLATE_ID = process.env.REACT_APP_EMAILJS_TEMPLATE_ID;
const EMAILJS_PUBLIC_KEY = process.env.REACT_APP_EMAILJS_PUBLIC_KEY;

// Admin emails to notify
const ADMIN_EMAILS = ['joelauge@gmail.com', 'pierre@augeinnovation.com'];

/**
 * Send user signup notification to admin emails
 * @param {Object} userData - User data from Clerk
 * @returns {Promise} Email response
 */
export const sendUserSignupNotification = async (userData) => {
  try {
    console.log(' DEBUG: Sending signup notification for user:', userData);

    // Prepare email data
    const emailData = {
      to_email: ADMIN_EMAILS.join(','),
      to_name: 'Auge Innovation Admin',
      from_name: 'Auge Innovation Client Portal',
      subject: ' New User Registration - Auge Innovation',
      user_name: `${userData.firstName || ''} ${userData.lastName || ''}`.trim() || 'Unknown User',
      user_email: userData.emailAddresses?.[0]?.emailAddress || 'No email provided',
      user_organization: userData.publicMetadata?.organization || 'Not specified',
      signup_date: new Date().toLocaleString(),
      user_id: userData.id || 'Unknown ID',
      message: `
        New user registration received:
        
        Name: ${userData.firstName || ''} ${userData.lastName || ''}
        Email: ${userData.emailAddresses?.[0]?.emailAddress || 'No email provided'}
        Organization: ${userData.publicMetadata?.organization || 'Not specified'}
        Signup Date: ${new Date().toLocaleString()}
        User ID: ${userData.id || 'Unknown ID'}
        
        This user is now pending approval in the admin dashboard.
      `
    };

    console.log(' Sending email with data:', emailData);

    // Send email using EmailJS
    const response = await emailjs.send(
      EMAILJS_SERVICE_ID,
      EMAILJS_TEMPLATE_ID,
      emailData,
      EMAILJS_PUBLIC_KEY
    );

    console.log(' Email sent successfully:', response);
    return response;

  } catch (error) {
    console.error(' Email sending failed:', error);
    throw error;
  }
};

/**
 * Send welcome email to new user
 * @param {Object} userData - User data from Clerk
 * @returns {Promise} Email response
 */
export const sendWelcomeEmail = async (userData) => {
  try {
    console.log(' DEBUG: Sending welcome email to user:', userData);

    const userEmail = userData.emailAddresses?.[0]?.emailAddress;
    if (!userEmail) {
      throw new Error('No email address found for user');
    }

    const emailData = {
      to_email: userEmail,
      to_name: `${userData.firstName || ''} ${userData.lastName || ''}`.trim() || 'Valued Client',
      from_name: 'Auge Innovation',
      subject: ' Welcome to Auge Innovation - Account Created',
      user_name: `${userData.firstName || ''} ${userData.lastName || ''}`.trim() || 'Valued Client',
      user_email: userEmail,
      signup_date: new Date().toLocaleString(),
      message: `
        Welcome to Auge Innovation!
        
        Your account has been successfully created and is currently pending approval.
        You will receive a notification once your account is approved.
        
        Thank you for choosing Auge Innovation for your advanced firearms training needs.
      `
    };

    console.log(' Sending welcome email with data:', emailData);

    const response = await emailjs.send(
      EMAILJS_SERVICE_ID,
      EMAILJS_TEMPLATE_ID,
      emailData,
      EMAILJS_PUBLIC_KEY
    );

    console.log(' Welcome email sent successfully:', response);
    return response;

  } catch (error) {
    console.error(' Welcome email sending failed:', error);
    throw error;
  }
};

/**
 * Send approval notification to user
 * @param {Object} userData - User data from Clerk
 * @param {boolean} approved - Whether the user was approved or rejected
 * @returns {Promise} Email response
 */
export const sendApprovalNotification = async (userData, approved = true) => {
  try {
    console.log(' DEBUG: Sending approval notification to user:', userData, 'Approved:', approved);

    const userEmail = userData.emailAddresses?.[0]?.emailAddress;
    if (!userEmail) {
      throw new Error('No email address found for user');
    }

    const emailData = {
      to_email: userEmail,
      to_name: `${userData.firstName || ''} ${userData.lastName || ''}`.trim() || 'Valued Client',
      from_name: 'Auge Innovation',
      subject: approved 
        ? ' Account Approved - Welcome to Auge Innovation' 
        : ' Account Status Update - Auge Innovation',
      user_name: `${userData.firstName || ''} ${userData.lastName || ''}`.trim() || 'Valued Client',
      user_email: userEmail,
      approval_date: new Date().toLocaleString(),
      message: approved 
        ? `
          Great news! Your account has been approved.
          
          You can now access the full features of the Auge Innovation client portal.
          Log in at https://augeinnovation.com to get started.
          
          Thank you for choosing Auge Innovation!
        `
        : `
          We regret to inform you that your account application requires additional review.
          
          Please contact us at support@augeinnovation.com for more information.
          
          Thank you for your interest in Auge Innovation.
        `
    };

    console.log(' Sending approval notification with data:', emailData);

    const response = await emailjs.send(
      EMAILJS_SERVICE_ID,
      EMAILJS_TEMPLATE_ID,
      emailData,
      EMAILJS_PUBLIC_KEY
    );

    console.log(' Approval notification sent successfully:', response);
    return response;

  } catch (error) {
    console.error(' Approval notification sending failed:', error);
    throw error;
  }
};

// Initialize EmailJS
if (EMAILJS_PUBLIC_KEY) {
  emailjs.init(EMAILJS_PUBLIC_KEY);
  console.log(' EmailJS initialized successfully');
} else {
  console.warn(' EmailJS public key not found. Email functionality will be disabled.');
} 